
// ---------------------------------
#mask {
  background: rgb(0, 0, 0);

  width: 100vw;
  height: 100vh;

  opacity: 0.7;

  position: fixed;

  top: 0;
  bottom: 0;
  right: 0;
  left: 0;

  z-index: 1000;
}
// ---------------------------------
#modal {
  background: #fff;
  color: #555;
  width: 550px;
  height: 600px;

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  margin: 0 auto;
  z-index: 9999;
}


// ---------------------------------

#mask.hidden {
  display: none;
}

#modal.hidden {
  display: none;
}

// ---------------------------------

.reservation-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
// ---------------------------------

.hotelーreservation {
  width: 100%;

  font-size: 20px;
  font-family: "游明朝", "YuMincho", serif;
  font-weight: normal;
  color: #000000;
  letter-spacing: 2px;
  line-height: 1;

  text-align: center;

  padding-top: 30px;
}

// ---------------------------------
//閉じるボタン
#close {
  cursor: pointer;
  width: auto;
  height:auto;
  font-size: 30px;
  text-align: center;
  color: rgb(34, 33, 33);

  position: absolute;
  top:20px;
  right: 20px;

}
